
import ButtonTab from 'vux/src/components/button-tab/button-tab.vue'
import ButtonTabItem from 'vux/src/components/button-tab/button-tab-item.vue'
import Swipeout from 'vux/src/components/swipeout/swipeout.vue'
import SwipeoutItem from 'vux/src/components/swipeout/swipeout-item.vue'
import SwipeoutButton from 'vux/src/components/swipeout/swipeout-button.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

export default {
name:'msg',
components:{
ButtonTab,ButtonTabItem,
Swipeout,SwipeoutItem,SwipeoutButton,
Group,Cell,
XButton
},
data(){
return {
hasReadMsgs:null,
hasNotReadMsgs:null,
tabIndex:0
}
},
created:function(){
this.getAllMsgs();
},
methods:{

getAllMsgs(){
var that=this;
this.$axios.get('/messages',{
params:{
accesstoken:window.localStorage.getItem('accesstoken')
}
}).then(function(res){

if(res.data.success){
that.hasReadMsgs=res.data.data.has_read_messages;
that.hasNotReadMsgs=res.data.data.hasnot_read_messages;
}
})
},

markRead(mid){
var that=this;
this.$axios.post('/message/mark_one/'+mid,{
accesstoken:window.localStorage.getItem('accesstoken')
}).then(function(res){

that.getAllMsgs();
})
},

markAll(){
var that=this;
this.$axios.post('/message/mark_all',{
accesstoken:window.localStorage.getItem('accesstoken')
}).then(function(res){


that.$router.go(0);
})
}
}

}



