//
import Vue from 'vue';
import Vuex from 'vuex';
Vue.use(Vuex);
const store =new Vuex.Store({
    state:{
        topics:[//tab切换
            {name:'all',desc:'全部'},//0
            {name:'good',desc:'精华'},//1
            {name:'share',desc:'分享'},
            {name:'ask',desc:'问答'},
            {name:'job',desc:'招聘'},
            {name:'dev',desc:'客户端测试'}
        ],
        isShowMoreMenus:false,  //actionsheet默认显示与否
        // moreMenus:      //如果是未登录状态，显示‘登录’，如果是已登录状态，显示个人中心，消息通知，登出
        isLogined:false,//登录状态，默认未登录
        unLoginedMenus:{//未登录状态的相关内容
            login:'登录',
        },
        loginedMenus:{//登录状态的相关内容
            center:'个人中心',
            create:'发表新话题',
            msg:'消息通知',
            logout:'登出'
        },
        loginname:'',//登录用户名
        accesstoken:"",//登录账号
        authorId:''//登录账号id
    },
    getters:{
        //进行登录状态判断，从而进行显示相对应的登录内容
        moreMenus(state){//参数state表示当前store对象的state数据
            if(state.isLogined){//默认未登录，显示未登录状态
                return state.loginedMenus
            }
            return state.unLoginedMenus
        }
    },
    mutations:{
        //修改actionsheet的显示与否
        mutationIsShowMoreMenus:function(state,payload){
            state.isShowMoreMenus=payload;
        },
        //修改登录状态
        mutationIsLogined:function(state){
            state.isLogined=true
        },
        //修改登录用户名
        mutationLoginname:function(state,payload){
            state.loginname=payload;
        },
        //修改登录accesstoken
        mutationAccesstoken:function(state,payload){
            state.accesstoken=payload;
        },
        //修改登录账号id
        mutationAuthorId:function(state,payload){
            state.authorId=payload;
        },
        //登出操作
        mutationLogout:function(state){
            state.isLogined=false;
            state.loginname='';
            state.accesstoken='';
            state.authorId='';
        }
    },
    actions:{}
})
export default store;


