export default {
    name:"center",
    //页面加载完成时，直接转换到user.vue
    created:function(){
        //replace直接替换，不具有记忆，即不可使用返回进行返回到center.vue
        this.$router.replace('/user/'+localStorage.getItem("loginname"));
    }
}



