
import Group from 'vux/src/components/group/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Toast from 'vux/src/components/toast/index.vue'

import {mavonEditor} from 'mavon-editor'
import 'mavon-editor/dist/css/index.css'
export default {
name:"edit",
props:['id'],
components:{
Group,XTextarea,
mavonEditor,
XButton,
Toast
},
data(){
return {
topic:{
title:'',
content:''
},
toolbars:{
bold: true, // 粗体
italic: true, // 斜体
header: true, // 标题
underline: true, // 下划线

mark: true, // 标记


quote: true, // 引用
ol: true, // 有序列表
ul: true, // 无序列表
link: true, // 链接
imagelink: true, // 图片链接
code: true, // code
table: true, // 表格
fullscreen: true, // 全屏编辑
readmodel: true, // 沉浸式阅读
htmlcode: true, // 展示html源码
help: true, // 帮助
/* 1.3.5 */
undo: true, // 上一步
redo: true, // 下一步
trash: true, // 清空
save: false, // 保存（触发events中的save事件）
/* 1.4.2 */
navigation: true, // 导航目录
/* 2.1.8 */
alignleft: true, // 左对齐
aligncenter: true, // 居中
alignright: true, // 右对齐
/* 2.2.1 */
subfield: true, // 单双栏模式
}
}
},
methods:{

edittopic:function(){
var that=this;
this.$axios.post('/topics/update',{
accesstoken:this.$store.state.accesstoken,
title:this.topic.title,
tab:'dev',
content:this.topic.content,
topic_id:this.topic.id
}).then(function(res){

that.$router.go(-1);

})
}
},
beforeRouteEnter:function(to,from,next){
next(function(vm){
vm.$axios.get('/topic/'+to.params.id).then(function(res){

vm.topic=res.data.data;
})
})
},
beforeRouteUpdate:function(to,from,next){
var that=this;
this.$axios.get('/topic/'+to.params.id).then(function(res){

that.topic=res.data.data;
})
next();
}
}



