
import Group from 'vux/src/components/group/index.vue'
import CellBox from 'vux/src/components/cell-box/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import GroupTitle from 'vux/src/components/group-title/index.vue'
import WechatEmotion from 'vux/src/components/wechat-emotion/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Popup from 'vux/src/components/popup/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import XTextarea from 'vux/src/components/x-textarea/index.vue'

import {mapState} from 'vuex'
export default {
name:'topic',
props:['id'],
directives: {
TransferDom
},
components:{
Group,
CellBox,
Flexbox,
FlexboxItem,
GroupTitle,
WechatEmotion,
XButton,
Popup,
XTextarea
},
data(){
return{
topic:null,
isShowPopup:false,
replyContent:'',
reply_id:null,
reply_loginname:null
}
},
computed:{
...mapState(['isLogined'])
},
methods:{

upOrDown(reply_id){
var that=this;
this.$axios.post('/reply/'+reply_id+'/ups',{
accesstoken:this.$store.state.accesstoken
}).then(function(res){

var type=res.data.action;

if(type=='up'){//实现点赞
var reply;
for(var i=0;i<that.topic.replies.length;i++){
if(reply_id==that.topic.replies[i].id){//找到点赞的评论
reply=that.topic.replies[i];
break;
}
}

reply.ups.push(that.$store.state.accesstoken);
}else if(type=='down'){//取消点赞
var reply;
for(var i=0;i<that.topic.replies.length;i++){
if(reply_id==that.topic.replies[i].id){//找到点赞的评论
reply=that.topic.replies[i];
break;
}
}


for(var a=0;a<reply.ups.length;a++){
if(reply.ups[a]==that.$store.state.accesstoken){
reply.ups.splice(a,1);
break;
}
}

}
})
},

collect:function(){
var that=this;
this.$axios.post('/topic_collect/collect',
{
accesstoken:this.$store.state.accesstoken,
topic_id:this.topic.id
}).then(function(res){

that.topic.is_collect=true;



})
},

decollect(){
var that=this;
this.$axios.post('/topic_collect/de_collect',
{
accesstoken:this.$store.state.accesstoken,
topic_id:this.topic.id
}).then(function(res){

that.topic.is_collect=false;



})
},

showPopup(replyId,loginname){
this.isShowPopup=true;
this.reply_id=replyId;
this.reply_loginname=loginname;
},

addReply(replyTab){
var params={
accesstoken:this.$store.state.accesstoken,
content:this.replyContent
}

if(replyTab==2){
params.reply_id=this.reply_id;
params.content="@"+this.reply_loginname+' '+params.content;
}
var that=this;
this.$axios.post('/topic/'+this.topic.id+'/replies',params).then(function(res){

if(res.data.success){




that.getTopic();
}

that.replyContent='';
that.isShowPopup=false;
that.reply_id='';
that.reply_loginname='';
})
},
getTopic(){
var that=this;
this.$axios.get('/topic/'+this.id).then(function(res){

that.topic=res.data.data;
})
}
},
beforeRouteEnter:function(to,from,next){
next(function(vm){
vm.getTopic();
})
}
}



