
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Scroller from 'vux/src/components/scroller/index.vue'
import ButtonTab from 'vux/src/components/button-tab/button-tab.vue'
import ButtonTabItem from 'vux/src/components/button-tab/button-tab-item.vue'

import {mapState} from 'vuex'
export default {
name:"topiclist",
props:['name'],
components:{
Cell,
Group,
Scroller,
ButtonTab,
ButtonTabItem
},
data(){
return {
lists:[],

pullupconfig:{
content:'上拉加载更多',
downContent:'松开后进行加载',
upContent:'加载更多',
loadingContent:'加载中...'
},
page:1,//当前页数
limit:6,//每页显示的数量

pulldangconfig:{
content:'下拉刷新更多',
downContent:'松开后进行加载',
upContent:'刷新更多',
loadingContent:'刷新中...'
}
}
},
computed:{
...mapState(['topics'])
},
methods:{

routeTo(index){

this.$router.push('/topiclist/'+this.topics[index].name);
},

loadMoreDate(){

this.page=this.page+1;
var that=this;
this.$axios.get('/topics',{
params:{
tab:this.name,
page:this.page,
limit:this.limit
}
}).then(function(res){








that.lists.push(...res.data.data);



that.$refs.pull.donePullup();
})
},

loadM(){

var that=this;
this.$axios.get('/topics',{
params:{
tab:this.name,
page:1,
limit:this.limit
}
}).then(function(res){

that.lists=res.data.data;



that.$refs.pull.donePulldown();
})
}
},



beforeRouteEnter(to,from,next){




next(function(vm){


vm.$axios.get('/topics',{
params:{
tab:to.params.name,
page:vm.page,
limit:vm.limit
}
}).then(function(res){

vm.lists=res.data.data;
})
})
},
beforeRouteUpdate(to,from,next){

var that=this;
this.$axios.get('/topics',{
params:{
tab:to.params.name,
page:this.page,
limit:this.limit
}
}).then(function(res){

that.lists=res.data.data;
}),
next();
}

}



